body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.toggle_button{
    display: flex;
    justify-content: space-around;
    flex-direction: column;    
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle_button:focus{
    outline: none;
}

.toggle_button_line {
    width: 30px;
    height: 3px;
    background: white;
}



.toolbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background : blue;
    height: 36px;
    
}
.spacer {
    flex: 1 1;
}
.toolbar_nav {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;

} 

.toolbar_logo{
    margin-left: 0.4rem;
}

.toolbar_logo a{
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
}

.toolbar_nav_items{
    colour: white;
}


.toolbar_nav_items ul {
    list-style: none;
    colour: white;
    margin: 0;
    padding: 0;
    display: flex;

}
.toolbar_nav_items a {
    colour: white;
    font-weight: 300;
}
.toolbar_nav_items li {
    color: white;
    padding: 0 0.2rem;
}

.toolbar_nav_items a:hover,
.toolbar_nav_items a:active {
    color: orange;
}

@media (max-width: 768px){
    .toolbar_nav_items{
        display: none;
    }

}

@media (min-width: 769px){
    .toolbar_toggle {
        display: none;
    }
    .toolbar_logo {
        margin-left: 0;
    }
}


.side_drawer {
    height: 100%;
    background-color: whitesmoke;
    box-shadow: 1px 0px 7px rgba(0,0,0,0.5);
    position: fixed;
    top:0;
    left: 0;
    width: 70%;
    max-width: 200;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side_drawer.show{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side_drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side_drawer li {
    margin: 0.5rem 0;

}

.side_drawer a {
    color: purple;
    text-decoration: none;
    font-size: 1.2rem;
}

.side_drawer a:hover,
.side_drawer a:active {
    color: #fa923f;
}


@media (min-width: 769px){
    .side_drawer {
        display: none;
    }
}


.backdrop{
    position : fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    z-index: 100;
    top: 0;
    left: 0;
}

